import { useState } from "react";
import { connect } from "react-redux";

import { HeadLine, PrimaryContainer, ErrorContainer } from "../UI/utils";
import { Spinner } from "../UI/spinner";

const scrollSnap = {
    container: {
        overflow: "auto",
        scrollSnapType: "inline mandatory",
    },
    item: { scrollSnapAlign: "center" },
};

const tabList = (experiences, openTab, setOpenTab) => (
    <div
        className="submain flex flex-row lg:flex-col"
        style={scrollSnap.container}
    >
        {experiences.map((experience, expIndex) => {
            return (
                <button
                    className={
                        "flex-none flex-grow text-center lg:text-left py-3 px-5 border-submain border-b-2 lg:border-b-0 lg:border-l-2 " +
                        (openTab === expIndex
                            ? "text-main bg-mainopaque rounded-t-md lg:rounded-none lg:rounded-r-md"
                            : "")
                    }
                    style={scrollSnap.item}
                    key={experience.company.substr(0, 4).trim()}
                    onClick={(e) => {
                        e.preventDefault();
                        setOpenTab(expIndex);
                    }}
                >
                    {experience.company}
                </button>
            );
        })}
    </div>
);

const getExpDescList = (descList) => {
    if (descList == null) return null;
    else
        return (
            <ul className="content pt-2 sm:list-disc sm:list-outside sm:pl-4">
                {descList.map((desc) => {
                    return (
                        <li className="pb-4" key={desc.substr(0, 4).trim()}>
                            {desc}
                        </li>
                    );
                })}
            </ul>
        );
};

const WorkExperience = (props) => {
    // state hook
    const [openTab, setOpenTab] = useState(0);

    let sectionContent = <Spinner />;

    if (props.errMsg !== "") {
        sectionContent = <ErrorContainer>{props.errMsg}</ErrorContainer>;
    }
    // if experiences not null then only exec
    else if (props.experiences !== null) {
        const workExp = props.experiences.map((experience, expIndex) => {
            return (
                <span
                    key={experience.company.substr(0, 4).trim()}
                    className={`${
                        openTab === expIndex ? `block` : `hidden`
                    } flex flex-col space-y-8`}
                >
                    {experience.positions
                        .map((position) => {
                            return (
                                <span
                                    className="flex flex-col space-y-2"
                                    key={position.title.substr(0, 4).trim()}
                                >
                                    <span className="heading">
                                        <span>{position.title}</span>
                                        <span className="text-main-color">
                                            &nbsp;&nbsp;@&nbsp;&nbsp;
                                            <a
                                                href={experience.url}
                                                rel="noopener noreferrer"
                                                target="_blank"
                                            >
                                                {experience.company}
                                            </a>
                                        </span>
                                    </span>
                                    <span className="submain">
                                        {position.date}
                                    </span>
                                    {getExpDescList(position.descList)}
                                </span>
                            );
                        })
                        .reverse()}
                </span>
            );
        });
        sectionContent = (
            <div className="flex flex-col lg:flex-row lg:space-x-6">
                <div role="tablist" className="lg:w-3/12 pb-5 lg:pb-0">
                    {tabList(props.experiences, openTab, setOpenTab)}
                </div>
                <div className="lg:w-9/12">{workExp}</div>
            </div>
        );
    }

    return (
        <PrimaryContainer>
            <HeadLine>Where I’ve Worked.</HeadLine>
            {sectionContent}
        </PrimaryContainer>
    );
};

const mapStateToProps = (state) => {
    return {
        experiences: state.home.experiences,
        errMsg: state.home.errorMsgs.experiencesErrMsg,
    };
};

export default connect(mapStateToProps, null)(WorkExperience);
