import { connect } from "react-redux";

import { HeadLine, SecondaryContainer, ErrorContainer } from "../UI/utils";
import { Spinner } from "../UI/spinner";
import { MyImage } from "../UI/myImage";
import { Location } from "../UI/icons";

const getSkills = (skills) => {
    return skills.map((skill) => {
        return (
            <span key={skill} className="h-auto w-auto align-bottom pb-2">
                -&nbsp;&nbsp;{skill}
            </span>
        );
    });
};

const AboutMe = (props) => {
    let sectionContent = <Spinner />;

    if (props.errMsg !== "") {
        sectionContent = <ErrorContainer>{props.errMsg}</ErrorContainer>;
    }
    // if aboutMe is not null then only exec
    else if (props.aboutMe !== null) {
        sectionContent = (
            <div className="flex flex-col justify-between lg:flex-row-reverse">
                <div className="w-full lg:w-4/12">
                    <MyImage
                        myImageUrl={props.aboutMe.myImage}
                        dotsUrl={props.aboutMe.dots}
                    />
                </div>
                <div className="content aboutMe flex flex-col space-y-6 lg:space-y-8 w-full pt-4 xs:pt-5 sm:pt-6 lg:pt-0 lg:w-8/12 lg:pr-10">
                    <span className="flex flex-col space-y-4">
                        {props.aboutMe.descList.map((desc) => {
                            return (
                                <span key={desc.substr(0, 6).trim()}>
                                    {desc}
                                </span>
                            );
                        })}
                    </span>
                    <span>
                        <span className="block pb-4">
                            Here are a few technologies / frameworks I've been
                            working with recently:
                        </span>
                        <span className="grid grid-cols-2 sm:grid-cols-3 text-left md:pl-4">
                            {getSkills(props.aboutMe.recentSkills)}
                        </span>
                    </span>
                    <span>
                        <span className="submain inline-flex align-middle space-x-2 pb-1 md:pb-2 lg:pb-3">
                            <Location customClasses="h-3 lg:h-4 my-auto" />
                            <span>Currently Living In</span>
                        </span>
                        <p>{props.aboutMe.currentLocation}</p>
                    </span>
                </div>
            </div>
        );
    }

    return (
        <SecondaryContainer>
            <HeadLine>About Me.</HeadLine>
            {sectionContent}
        </SecondaryContainer>
    );
};

const mapStateToProps = (state) => {
    return {
        aboutMe: state.home.aboutMe,
        errMsg: state.home.errorMsgs.aboutMeErrMsg,
    };
};

export default connect(mapStateToProps, null)(AboutMe);
