import * as actionTypes from "./actionTypes";
import { firebaseAxiosInstance, mediumAxiosInstance } from "../../base-axios";
import LocalStorage from "../../utils/localStorage";

const fetchDetailsFailed = (errObj) => {
    return {
        type: actionTypes.FETCH_DETAILS_FAILED,
        error: errObj,
    };
};

// -------------------------------------------------------------------
// ---------------------- fetch the aboutMe data ---------------------
// -------------------------------------------------------------------
const fetchAboutMe = (aboutMe) => {
    return {
        type: actionTypes.FETCH_ABOUTME,
        aboutMe: aboutMe,
    };
};
export const initAboutMe = () => (dispatch) => {
    const prevState = LocalStorage.get("aboutMe");
    if (prevState !== null) return dispatch(fetchAboutMe(prevState));

    firebaseAxiosInstance
        .get("/aboutMe.json")
        .then((res) => {
            dispatch(fetchAboutMe(res.data));
            LocalStorage.set("aboutMe", res.data);
        })
        .catch((err) => {
            const errObj = {
                type: "aboutMeErrMsg",
                msg: err.toString(),
            };
            dispatch(fetchDetailsFailed(errObj));
        });
};

// -------------------------------------------------------------------
// ------------------ fetch the experiences/job data -----------------
// -------------------------------------------------------------------
const fetchExperiences = (experiences) => {
    return {
        type: actionTypes.FETCH_EXPERIENCES,
        experiences: experiences,
    };
};
export const initExperiences = () => (dispatch) => {
    const prevState = LocalStorage.get("experiences");
    if (prevState !== null) return dispatch(fetchExperiences(prevState));

    firebaseAxiosInstance
        .get("/experiences.json")
        .then((res) => {
            res.data.reverse();
            dispatch(fetchExperiences(res.data));
            LocalStorage.set("experiences", res.data);
        })
        .catch((err) => {
            const errObj = {
                type: "experiencesErrMsg",
                msg: err.toString(),
            };
            dispatch(fetchDetailsFailed(errObj));
        });
};

// -------------------------------------------------------------------
// ---------------- fetch the latest 4/5 Medium Blogs  ---------------
// -------------------------------------------------------------------
const fetchBlogs = (blogs) => {
    return {
        type: actionTypes.FETCH_BLOGS,
        blogs: blogs,
    };
};
export const initBlogs = () => (dispatch) => {
    const prevState = LocalStorage.get("blogs");
    if (prevState !== null) return dispatch(fetchBlogs(prevState));

    mediumAxiosInstance
        .get(
            "/api.json?rss_url=https%3A%2F%2Fmedium.com%2Ffeed%2F%40dhyeythumar"
        )
        .then((res) => {
            if (res.data.status === "ok") {
                const blogs = [];
                res.data.items.forEach((blog) => {
                    blogs.push({
                        title: blog.title,
                        url: blog.link,
                        // only select 3 tags
                        tags: blog.categories.slice(0, 3),
                    });
                });
                dispatch(fetchBlogs(blogs));
                LocalStorage.set("blogs", blogs);
            } else {
                throw Error(res.data.message);
            }
        })
        .catch((err) => {
            const errObj = {
                type: "blogsErrMsg",
                msg: err.toString(),
            };
            dispatch(fetchDetailsFailed(errObj));
        });
};
