import { connect } from "react-redux";

import { HeadLine, PrimaryContainer, ErrorContainer } from "../UI/utils";
import { Spinner } from "../UI/spinner";

const KnowMore = (props) => {
    let sectionContent = <Spinner />;

    if (props.errMsg !== "") {
        sectionContent = <ErrorContainer>{props.errMsg}</ErrorContainer>;
    }
    // if blogs is not null then only exec
    else if (props.blogs !== null) {
        sectionContent = (
            <div>
                <p className="submain pb-4 sm:pb-6">Latest Blogs</p>
                <ul
                    className="content list-disc list-outside pl-4 flex flex-col space-y-4 sm:space-y-6 w-auto lg:w-max"
                    style={{ textAlign: "left" }}
                >
                    {props.blogs.map((blog, index) => (
                        <li key={index}>
                            <a
                                href={blog.url}
                                rel="noopener noreferrer"
                                target="_blank"
                                className="block sm:pb-2 hover:text-main"
                            >
                                {blog.title}.
                            </a>
                            <p className="hidden sm:flex flex-row space-x-5 submain">
                                {blog.tags.map((tag) => (
                                    <span key={tag}>{tag}</span>
                                ))}
                            </p>
                        </li>
                    ))}
                </ul>
            </div>
        );
    }

    return (
        <PrimaryContainer>
            <HeadLine>Know More.</HeadLine>
            {sectionContent}
        </PrimaryContainer>
    );
};

const mapStateToProps = (state) => {
    return {
        blogs: state.home.blogs,
        errMsg: state.home.errorMsgs.blogsErrMsg,
    };
};

export default connect(mapStateToProps, null)(KnowMore);
