import { Component } from "react";
import { connect } from "react-redux";
import { initAboutMe, initExperiences, initBlogs } from "../store/actions/home";

import Hero from "../components/Home/Hero";
import AboutMe from "../components/Home/AboutMe";
import WorkExperience from "../components/Home/WorkExperience";
import KnowMore from "../components/Home/KnowMore";

class Home extends Component {
    // fetch the data for home page & load in the redux store
    componentDidMount() {
        document.title = "Home | Dhyey Thumar";

        this.props.fetchAboutMeData();
        this.props.fetchExperiencesData();
        this.props.fetchBlogsData();
    }

    render() {
        return (
            <>
                {/* welcome/hero section */}
                <Hero />

                {/* about-me section */}
                <AboutMe />

                {/* experience/job section */}
                <WorkExperience />

                {/* know more section to diplay latest blogs topics & etc */}
                <KnowMore />
            </>
        );
    }
}

// dispatch actions
const mapDispatchToProps = (dispatch) => {
    return {
        fetchAboutMeData: () => dispatch(initAboutMe()),
        fetchExperiencesData: () => dispatch(initExperiences()),
        fetchBlogsData: () => dispatch(initBlogs()),
    };
};

export default connect(null, mapDispatchToProps)(Home);
